export default {
  ////WEB3
  getLocalWeb3(state, payload) {
    const result = payload.result;

    const web3Copy = state.web3;
    console.dir(result.web3)
    web3Copy.instance = () => result.web3;
    // web3Copy.address = result.address
    //   ? result.address.toString()
    //   : web3Copy.address;
    // web3Copy.coinbase = result.coinbase
    //   ? result.coinbase.toString()
    //   : web3Copy.coinbase;
    web3Copy.networkId = APPROVED_NETWORK_ID;
    //   ? result.networkId.toString()
    //   : web3Copy.networkId;
    // web3Copy.error = result.web3Error ? result.web3Error : web3Copy.error;
    // web3Copy.isInjected = result.hasInjectedWeb3
    //   ? result.hasInjectedWeb3
    //   : web3Copy.isInjected;

    state.web3 = web3Copy;
    console.dir(state.web3.instance())
    console.log(state)
    if (payload.callback) payload.callback(state);
  },
  ////WEB3
  [MUTATION_TYPES.REGISTER_WEB3_INSTANCE](state, payload) {
    const result = payload.result;

    const web3Copy = state.web3;
    web3Copy.instance = () => result.web3;
    web3Copy.address = result.address
      ? result.address.toString()
      : web3Copy.address;
    web3Copy.coinbase = result.coinbase
      ? result.coinbase.toString()
      : web3Copy.coinbase;
    web3Copy.networkId = result.networkId
      ? result.networkId.toString()
      : web3Copy.networkId;
    web3Copy.error = result.web3Error ? result.web3Error : web3Copy.error;
    web3Copy.isInjected = result.hasInjectedWeb3
      ? result.hasInjectedWeb3
      : web3Copy.isInjected;

    state.web3 = web3Copy;
    console.log(state)
    if (payload.callback) payload.callback(state);
  },
  ////
  [MUTATION_TYPES.UPDATE_USER_BLOCKCHAIN_STATUS](state, payload) {
    //
    const hasWeb3InjectedBrowser = state.web3.isInjected;
    const hasCoinbase = !!(state.web3.coinbase && state.web3.coinbase !== "");
    const coinbase = state.web3.coinbase;
    const approvedNetworkId = APPROVED_NETWORK_ID || state.web3.networkId;
    const isConnectedToApprovedNetwork = !!(
      state.web3.networkId &&
      state.web3.networkId !== "" &&
      state.web3.networkId === approvedNetworkId
    );
    const web3Status = {
      hasWeb3InjectedBrowser,
      hasCoinbase,
      isConnectedToApprovedNetwork,
      coinbase
    };

    let warningMessage = null;
    if (hasWeb3InjectedBrowser) {
      if (hasCoinbase) {
        if (!isConnectedToApprovedNetwork) {
          // 
          warningMessage = `You're not on the same Blockchain as us. Please connect to the ${NETWORKS[approvedNetworkId]}`;
        }
      } else {
        warningMessage =
          //Web3 MetamaskMetamask
          "Looks like you haven't logged into your Web3 injector. If you're using Metamask and you'd signed up, please log into Metamask, else click on the 'Sign Up' link above to begin.";
      }
    } else {
      //Web3
      warningMessage = "Your browser is not Web3-enabled.";
    }

    if (hasWeb3InjectedBrowser && hasCoinbase && isConnectedToApprovedNetwork) {
      const userCopy = state.user;
      Object.assign(userCopy, web3Status);
      state.user = userCopy;
    } else {
      //user
      resetUser(state, web3Status);
      //newsage
      resetNewsage(state);
    }

    if (payload.callback)
      payload.callback({ status: !warningMessage, warningMessage });
  },
  //Web3   
  [MUTATION_TYPES.UPDATE_WEB3_PROPERTIES](state, payload) {
    for (var i = payload.properties.length - 1; i >= 0; i--) {
      state.web3[payload.properties[i]] = payload.values[i];
      if (state.user[payload.properties[i]])
        state.user[payload.properties[i]] = payload.values[i];
    }

  },

  //
  [MUTATION_TYPES.SET_CURRENT_VIEW](state, newRoute) {
    state.currentView = newRoute.meta.view;
  },
  //
  [MUTATION_TYPES.UPDATE_USER_GRAVATAR](state, payload) {
    const userCopy = state.user;
    updateUserGravatar(state, userCopy, payload);
  },
  //DApp
  [MUTATION_TYPES.UPDATE_DAPP_READINESS](state, isReady) {
    state.isDAppReady = isReady;
  },
  //
  [MUTATION_TYPES.INITIALISE_IS_VALID_USER_BUT](state, payload) {
    state.isValidUserBut = payload.value ? "1" : "0";
    state.originalIsValidUserBut = payload.value ? "1" : "0";
    const userCopy = state.user;
    userCopy.warningMessage = payload.value ? payload.value : "";
    state.user = userCopy;
    if (payload.callback) payload.callback();
  },
  //
  [MUTATION_TYPES.SET_IS_VALID_USER_BUT](state, newValue) {
    state.isValidUserBut = newValue;
  },
  //
  [MUTATION_TYPES.RESET_IS_VALID_USER_BUT](state) {
    state.isValidUserBut = state.originalIsValidUserBut;
  },
  //
  [MUTATION_TYPES.LOGIN](state, payload) {
    const userObject = payload.userObject;
    const userCopy = state.user;

    Object.assign(userCopy, userObject, {
      isValid: true,
      firstName: userObject.firstName || "",
      lastName: userObject.lastName || "",
      email: userObject.email || "",
      gravatar: userObject.gravatar || "",
      isLoggedIn:
        !!(userObject.email && userObject.email !== "") &&
        state.user.hasCoinbase &&
        state.user.isConnectedToApprovedNetwork
    });

    getUserBalance(state).then(balance => {
      userCopy.balance = balance;
      state.user = userCopy;
      if (payload.callback) payload.callback(true);
    });



  },
  //
  [MUTATION_TYPES.LOGOUT](state, payload) {
    const hasWeb3InjectedBrowser = state.web3.isInjected;
    const hasCoinbase = !!(state.web3.coinbase && state.web3.coinbase !== "");
    const coinbase = state.web3.coinbase;
    const approvedNetworkId = APPROVED_NETWORK_ID || state.web3.networkId;
    const isConnectedToApprovedNetwork = !!(
      state.web3.networkId &&
      state.web3.networkId !== "" &&
      state.web3.networkId === approvedNetworkId
    );
    const web3Status = {
      hasWeb3InjectedBrowser,
      hasCoinbase,
      isConnectedToApprovedNetwork,
      coinbase
    };

    resetUser(state, web3Status);
    if (payload.callback) payload.callback();
  },
  //
  [MUTATION_TYPES.CHANGE_CURRENT_ROUTE_TO](state, newRoute) {
    state.currentRoute = newRoute;
  },

  //NEWSAGE
  [MUTATION_TYPES.INIT_NEWSAGE](state, payload) {
    const newsageObject = payload.newsage;
    const newsageCopy = state.newsage;
    Object.assign(newsageCopy, newsageObject, {});
    state.newsage = newsageCopy;
    if (payload.callback) payload.callback(true);
  },

  //INIT_FACTORY
  [MUTATION_TYPES.INIT_FACTORY](state, payload) {
    const factoryObject = payload.factory;
    const factoryCopy = state.factory;
    Object.assign(factoryCopy, factoryObject, {});
    state.factory = factoryCopy; 
    if(state.factory.NewSageRouterAddress.length>20){
      getAddressBalance(state,state.factory.NewSageRouterAddress).then(balance => {
        
        state.newSageRouter.ethBalance = balance;
        //if (payload.callback) payload.callback(true);
      });
    }
    
    if (payload.callback) payload.callback(true);
  },
  //INIT_NEWSAGE_ROUTER
  [MUTATION_TYPES.INIT_NEWSAGE_ROUTER](state, payload) {
    const newSageRouterObject = payload.newSageRouter;
    const newSageRouterCopy = state.newSageRouter;
    Object.assign(newSageRouterCopy, newSageRouterObject, {});
    state.newSageRouter = newSageRouterCopy;
    if (payload.callback) payload.callback(true);
  },
  //INIT_NEWTOKEN
  [MUTATION_TYPES.INIT_NEWTOKEN](state, payload) {
    const newTokeObject = payload.newToke;
    const newTokeCopy = state.newToke;
    Object.assign(newTokeCopy, newTokeObject, {});
    state.newToke = newTokeCopy;
    if (payload.callback) payload.callback(true);
  },
  //INIT_NEWTOKEN
  [MUTATION_TYPES.INIT_USER_DATA](state, payload) {
    const newTokeObject = payload.matrixdata;
    const newTokeCopy = state.matrixdata;
    Object.assign(newTokeCopy, newTokeObject, {});
    state.matrixdata = newTokeCopy;
    if (payload.callback) payload.callback(true);
  },

  [MUTATION_TYPES.UPDATE_LANGUAGE](state, payload) {
    state.language=payload.language;
    if (payload.callback) payload.callback(true);
  },
  
  displayLoding(state, payload) {
    state.isloading=payload.isloading;
    if (payload.callback) payload.callback(true);
  },
  randomDisplayData(state, payload) {
    state.randomDisplay=payload.randomDisplay;
    if (payload.callback) payload.callback(true);
  },
  displayTips(state, payload) {
    state.isTips=payload.isTips;
    state.tipsMs=payload.tipsMs;
    if (payload.callback) payload.callback(true);
  },
  upmockBetting(state, payload) {
    state.mockBetting=payload.mockBetting;
    if (payload.callback) payload.callback(true);
  },
};
