  //
  accessBlockchain(state = null, data = {}, value = "",pairtType = null) {
    // 
    const blockchainData = Object.assign({}, data);
    //abi
    const contractToUse = blockchainData.contractIndexToUse
      ? newsageManager.getContractToUse()[blockchainData.contractIndexToUse]//
      : null;
    const blockchainMethodName = blockchainData.methodName;//
    //
    delete blockchainData.contractIndexToUse;
    delete blockchainData.methodName;
    switch (contractToUse.contractName){
      case "NewSageRouter":
        contractToUse.networks={
          "3": {
            "address": state.factory.NewSageRouterAddress,
          }
        }
        
        break;
      case "SmartMatrixNewsage":
        contractToUse.networks={
          "3": {
            "address": state.factory.SmartMatrixNewsageAddress,
          }
        }
        break;
      case "NewToken":
        contractToUse.networks={
          "3": {
              "address": state.factory.DETOAddress,
          }
        }  
        break;
      case "IuniswapV2Pair":
        if(pairtType=='WETH_DAI_PAIR'){//WETH_DETO_PAIR
            contractToUse.networks={
              "3": {
                  "address": WETH_DAI_PAIR.PAIR,
              }
            }
        }else{
          contractToUse.networks={
            "3": {
                "address": state.factory.uniswapV2PairAddress,
            }
          }
        }
      
        break; 
    }
    //
    return blockchainManager.querySmartContract({
      contractToUse: contractToUse ,//abi
      smartContractMethod: blockchainMethodName,//
      smartContractMethodParams: coinbase => {
        const blockchainPayload =
          value !== "" ? { from: coinbase, value } : { from: coinbase };
        return [...Object.values(blockchainData), blockchainPayload];
      },
      state,
      smartContractResolve: result => result,
      smartContractReject: error => error
    });
  }



